import { ISubscribedEvent } from '../../interfaces/actions/EventManager';

/**
 * Helper class for managing event subscriptions.
 * Meant to prevent callbacks to removed components.
 */
export class EventManager {
  /**
   * List of events registered for the instance
   */
  private subscribedEvents: ISubscribedEvent[] = [];

  /**
   * Register a new event listener
   */
  public registerEventListener(element: Element, event: string, callback: (...args: any[] | any) => void) {
    element.addEventListener(event, callback);
    this.subscribedEvents.push({
      element: element,
      name: event,
      callback: callback
    });
  }

  /**
   * Clear all event listeners from the instance
   */
  public clearEventListeners(): void {
    this.subscribedEvents.forEach((event: ISubscribedEvent) => {
        event.element.removeEventListener(event.name, event.callback);
    });
  }
}
